<template>
	<div class="register">
		<div class="box">
			<p class="Icon" @click="back()"><i class="fa fa-arrow-left"></i></p>
			<p class="title">猫眼电影</p>
		</div>
		<div class="Login_page">
			<div class="login_input">
				<p><input type="text" placeholder="账户名/手机号/Email" class="inp" @blur="fun()" v-model="username"/></p>
				<p><input type="password" placeholder="请输入你的密码" class="inp" v-model="passworld"/></p>
			</div>
			<div class="Login_button">
				<p><button @click="register">注册</button></p>
			</div>
		</div>
		
	</div>
</template>

<script>
	export default{
		data(){
			return {
				username:"",
				passworld:""
			}
		},
		methods:{
			back(){
				this.$router.push("/mine")
			},
			fun(){
				var reg=/^1(?:3\d|4[4-9]|5[0-35-9]|6[67]|7[013-8]|8\d|9\d)\d{8}$|^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;
				if(!reg.test(this.username)){
					alert("请输入正确的用户名");
					this.username=""
				}
			},
			register(){
				this.$http.get("http://localhost:3000/list?username="+this.username).then(res=>{
					if(res.data.length==0){
						this.$http.post("http://localhost:3000/list",{
							username:this.username,
							passworld:this.passworld
						},{
							Headers:{ "content-type":"application/json"}
						}).then(res=>{
							alert("注册成功");
							this.username="";
							this.passworld="";
							this.$router.push("/mine")
						})
					}else{
						alert("用户名已存在");
						this.username="",
						this.passworld=""
						
					}
				})
			}
		}
	}
</script>

<style scoped>
	.register{
		width: 100%;
		height: 60px;
	}
	.register .box{
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: space-between;
		background: #df2d2d;
		color: #fff;
		font-size: 20px;
		text-align: center;
		line-height: 60px;
	}
	.mine .Icon{
		width: 20%;
		height: 60px;
	}
	.register .title{
		width: 80%;
		height: 100%;
	}
	.register .Login_page{
		width:100%;
		height: 125px;
		margin-top:60px;
	}
	.register .Login_page .login_input{
		width: 100%;
		height: 80px;
		
	}
	.register .Login_page .login_input p input{
		width: 100%;
		height: 40px;
		border:none;
		border-top: 1px solid #ccc;
		border-bottom: 1px solid #ccc;
		font-size: 18px;
		padding: 10px;
	}
	.register .Login_page .Login_button{
		width: 100%;
		height: 30px;
	}
	.register .Login_page .Login_button button{
		width: 100%;
		height: 45px;
		font-size: 24px;
		line-height: 24px;
		background: #DF2D2D;
		color: #fff;
		font-family: "微软雅黑";
	}
</style>


